<template>
  <div>
    <el-card>
      <!-- 搜索框（学科名称） -->
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="目录名称" style="float: left">
          <el-input
            v-model="form.directoryName"
            style="width: 100%; margin-right: 10px"
          ></el-input>
        </el-form-item>

        <el-form-item label="状态" style="float: left">
          <el-select v-model="form.state" placeholder="请选择">
            <el-option label="启用" value="0"></el-option>
            <el-option label="禁用" value="1"></el-option>
          </el-select>
        </el-form-item>

        <el-button style="margin-left:10px" @click="cleanBtn">清除</el-button>
        <el-button type="primary" @click="cksearchBtn">搜索</el-button>

        <el-button
          class="xuekean"
          type="success"
          icon="el-icon-edit"
          @click="dialogVisibles = true"
          >新增学科</el-button
        >
      </el-form>
      <!-- 消息提示文案 -->
      <el-alert
        style="margin-bottom: 15px;"
        :title="`数据一共${total}条`"
        type="info"
        show-icon
      ></el-alert>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号"> </el-table-column>
        <el-table-column prop="subjectName" label="所属学科"></el-table-column>
        <el-table-column prop="directoryName" label="目录名称">
        </el-table-column>
        <el-table-column prop="username" label="创建者" width="200">
        </el-table-column>
        <el-table-column prop="addDate" label="创建日期" width="200">
          <template slot-scope="scope">
            <span>{{
              scope.row.addDate | parseTimeByString('{y}-{m}-{d} {h}:{i}:{s}')
            }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="totals" label="面试题数量"> </el-table-column>
        <el-table-column prop="state" label="状态">
          <template slot-scope="scope">
            <span> {{ scope.row.state == 0 ? '已启用' : '已禁用' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200">
          <template slot-scope="scope">
            <el-button
              type="text"
              class="cz"
              @click="startBtn(scope.row.id, scope.row.state)"
              >{{ scope.row.state !== 0 ? '启用' : '禁用' }}</el-button
            >
            <el-button
              type="text"
              class="cz"
              :disabled="scope.row.state == 0 ? false : true"
              @click="amendBtn(scope.row)"
              >修改</el-button
            >
            <el-button
              type="text"
              class="cz"
              :disabled="scope.row.state == 0 ? false : true"
              @click="deleteBtn(scope.row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination
        class="pages"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="100"
        layout="  prev, pager, next,sizes, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- 点击修改弹出的对话框 -->
    <el-dialog
      title="修改目录"
      :visible.sync="dialogVisible"
      width="30%"
      @close="onClose"
    >
      <!-- 弹出框的表单 -->
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- 学科选择框 -->
        <el-form-item label="所属学科" prop="subjectID">
          <el-select
            placeholder="请选择"
            v-model="ruleForm.subjectID"
            style="width:278px"
          >
            <el-option
              v-for="(item, index) in ruleData"
              :key="index"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学科名称" prop=" directoryName">
          <el-input v-model="ruleForm.directoryName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 点击新增的对话框 -->
    <el-dialog title="新增目录" :visible.sync="dialogVisibles" width="30%">
      <!-- 弹出框的表单 -->
      <el-form
        :model="ruleForms"
        :rules="rules"
        ref="ruleForms"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- 学科选择框 -->
        <el-form-item label="所属学科" prop="subjectID">
          <el-select
            placeholder="请选择"
            v-model="ruleForms.subjectID"
            style="width:278px"
          >
            <el-option
              v-for="(item, index) in ruleData"
              :key="index"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="学科名称" prop=" directoryName">
          <el-input v-model="ruleForms.directoryName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibles = false">取 消</el-button>
        <el-button type="primary" @click="addSubmitForm('ruleForms')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 引入Api
import { list, changeState, update, remove, add } from '@/api/hmmm/directorys'
import { simple } from '@/api/hmmm/subjects'
export default {
  data () {
    return {
      // 搜索的表单值
      form: {
        // 头部搜索框的值
        directoryName: '',
        // 选择框的值
        state: null,
        page: 1,
        pagesize: 10
      },
      // 表格数据
      tableData: [],
      // 选择框数据的渲染
      ruleData: [],
      // 对话框数据
      dialogVisible: false,
      dialogVisibles: false,
      // 弹出框
      rules: {
        directoryName: [
          { required: true, message: '请输入修改内容', trigger: 'blur' }
        ],
        subjectID: [
          { required: true, message: '请选择活动区域', trigger: 'change' }
        ]
      },
      ruleForm: {
        // 弹出框表单值
        directoryName: '',
        // 点击修改的id
        id: null,
        subjectID: null
      },
      ruleForms: {
        // 弹出框表单值
        directoryName: '',
        subjectID: null
      },
      // 分页
      total: 0
    }
  },
  created () {
    this.directorysApi()
    this.ruleApi()
  },
  methods: {
    // 获取数据的函数
    async directorysApi () {
      const { data } = await list(this.form)
      console.log(data)
      this.tableData = data.items
      this.total = data.counts
    },
    // 弹框选择框的渲染数据
    async ruleApi () {
      const { data } = await simple()
      console.log(data, 2222)
      this.ruleData = data
    },
    // 点击删除按钮
    deleteBtn (id) {
      // console.log(id)
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          // 点击删除对应的内容
          remove({ id }),
          // 刷新页面
          this.directorysApi()
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    },
    // 修改弹出框的提交表单事件
    submitForm (formName) {
      this.$refs[formName].validate(async valid => {
        console.log(valid)
        if (valid) {
          // 发送修改的请求
          const { directoryName, id, subjectID } = this.ruleForm
          await update({ directoryName, id, subjectID })
          this.dialogVisible = false
          // 刷新页面
          this.directorysApi()
        } else {
          this.$message({ message: '操作失败' })
          return false
        }
      })
    },
    // 新增弹出框的提交表单事件
    addSubmitForm (formName) {
      console.log(this.ruleForms)
      this.$refs[formName].validate(async valid => {
        console.log(valid)
        if (valid) {
          // 发送增加的请求
          await add(this.ruleForms)
          this.dialogVisibles = false
          this.$refs[formName].$el.reset()
          // 刷新页面
          this.directorysApi()
        } else {
          this.$message({ message: '操作失败' })
          return false
        }
      })
    },
    // 分页
    handleSizeChange (val) {
      // this.directorysApi({ pagesize: val })
      this.form.pagesize = val
      this.directorysApi()
    },
    handleCurrentChange (val) {
      // this.directorysApi({ page: val })
      this.form.page = val
      this.directorysApi()
    },
    // 启用禁用按钮
    async startBtn (id, state) {
      console.log(id, state)
      await changeState({ id, state: state == 0 ? '1' : '0' })
      this.directorysApi()
    },
    // 点击修改按钮的操作
    amendBtn (obj) {
      this.dialogVisible = true
      // 给当前点击修改的input框赋值
      this.ruleForm = obj
      console.log(this.ruleForm)
      // isFrontDisplay == 0?'true':'fales'
    },
    // 点击头部搜索按钮
    async cksearchBtn () {
      this.directorysApi()
    },
    // 点击清除
    cleanBtn () {
      this.form.directoryName = ''
      this.form.state = null
    },
    // 关闭对话框的回调
    onClose () {
      // 清空表单值
      this.ruleForm.subjectID = null
      this.ruleForm.directoryName = ''
      this.ruleForm.id = null
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.xuekean {
  float: right;
}
.cz {
  margin: 0 3px;
  cursor: pointer;
}
.el-input__inner {
  margin: 0 10px;
}
.pages {
  float: right;
  margin: 10px 0;
}

::v-deep .el-dialog__footer {
  text-align: right;
}
</style>
